.bannerContainer {
  position: relative;
  width: 100%;
  height: 240px;
  background-color: #1771dd;
  margin-top: 80px;
  display: flex;
  justify-content: center;
}

h2 {
  font-size: 2.5em;
  font-weight: 900;
  color: #ffffff;
  margin: 0px;
}

p.jobSubtitle {
  font-size: 1.5em;
  color: #ffffff;
  font-weight: 400;
  margin-top: 4px;
}

.bannerWrapper {
  margin: 0 auto;
  max-width: 1110px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 39%;
}

.graphicsTop {
  height: 154px;
  width: 154px;
  position: absolute;
  left: 126px;
  top: -20px;
}
